import React, { forwardRef } from "react"
import fotoRodri from "./media/rori.jpg"
import fotoMario from "./media/mario.png"
import { useForm, Controller } from "react-hook-form";

function enviarMail() {
  axios.post("http://serviciosapi1.alfabeta.net/alfabetaws/basews", data, config)
    .then(function (response) {
      console.log(response.data);
    })
    .catch(function (error) {
      console.log(error);
    });
}

function Formulario() {

  // ENVIAR FORMULARIO
  const locale = {
    noResultsText: "No existen resultados",
    placeholder: "",
    searchPlaceholder: "Buscar...",
    checkAll: "",
    yesterday: "Ayer",
    today: "Hoy",
    dateLocale: "es_AR",
  };
  const {
    register,
    formState: { errors },
    getValues,
    handleSubmit,
    reset,
    control,
  } = useForm({
    mode: "onChange",
  });
  const onSubmit = (data) => {
    enviarMail(data)
  };

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)} className="container-form">

        <Controller
          name="nombre"
          control={control}
          rules={{ required: true }}
          render={({ field: { ref, ...field } }) => (
            <input
              className="input-contacto"
              {...field}
              locale={locale}
              placeholder="Nombre y apellido"
              inputRef={ref}
            />
          )}
        />
        {errors.nombre && (<p className="error">Campo requerido</p>)}

        <Controller
          name="telefono"
          control={control}
          rules={{ required: true }}
          render={({ field: { ref, ...field } }) => (
            <input
              className="input-contacto"
              {...field}
              locale={locale}
              placeholder="Telfono"
              inputRef={ref}
            />
          )}
        />
        {errors.telefono && (<p className="error">Campo requerido</p>)}

        <Controller
          name="email"
          control={control}
          rules={{ required: true }}
          render={({ field: { ref, ...field } }) => (
            <input
              className="input-contacto"
              {...field}
              locale={locale}
              placeholder="Correo electrnico"
              inputRef={ref}
            />
          )}
        />
        {errors.email && (<p className="error">Campo requerido</p>)}

        <Controller
          name="empresa"
          control={control}
          rules={{ required: true }}
          render={({ field: { ref, ...field } }) => (
            <input
              className="input-contacto"
              {...field}
              locale={locale}
              placeholder="Empresa"
              inputRef={ref}
            />
          )}
        />
        {errors.empresa && (<p className="error">Campo requerido</p>)}

        <Controller
          name="asunto"
          control={control}
          rules={{ required: true }}
          render={({ field: { ref, ...field } }) => (
            <input
              className="input-contacto"
              {...field}
              locale={locale}
              placeholder="Asunto"
              inputRef={ref}
            />
          )}
        />
        {errors.asunto && (<p className="error">Campo requerido</p>)}

        <Controller
          name="mensaje"
          control={control}
          rules={{ required: true }}
          render={({ field: { ref, ...field } }) => (
            <textarea
              className="input-contacto area"
              {...field}
              locale={locale}
              placeholder="Mensaje"
              inputRef={ref}
            ></textarea>
          )}
        />
        {errors.mensaje && (<p className="error">Campo requerido</p>)}
        <br></br>

        <div className="container-enviar">
          <button className="button-enviar" type="submit" > Enviar </button>
        </div>
      </form>
    </>
  )
}


